import {
  Component,
  trigger,
  state,
  style,
  transition,
  animate,
  keyframes
} from '@angular/core';
import { CoursesComponent } from './courses/courses.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styles: [`
    #introduction{
      max-width: 960px;
      margin: 0 auto;
    }
    #image{
      display: block;
      margin-left: 10%;
      width: 20%;
      float: left;
      margin-top: 20vh;
    }
    #asd{
      width: 100%;
    }
    #details{
      display: block;
      padding-top: 0px;
      width: 70%;
      padding-left: 20px;
      float: left;
      margin-top: 20vh;
    }
    #nav-icon4 {
      width: 30px;
      height: 32px;
      position: fixed;
      top: 25px;
      right: 20px;
      transform: rotate(0deg);
      transition: .5s ease-in-out;
      cursor: pointer;
      z-index: 4;
    }
    #nav-icon4 span {
      display: block;
      position: absolute;
      height: 4px;
      width: 100%;
      background: rgb(93, 93, 93);
      border-radius: 4px;
      opacity: 1;
      left: 0;
      z-index: 4;
      transform: rotate(0deg);
    }
    a{
      color: rgb(93, 93, 93);
      transition: 250ms ease;
    }
    a:hover{
      color: #009688;
      transition: 250ms ease;
    }
    @media screen and (max-width: 768px){
        #image{
            width: 50%;
            margin-left: 0%;
        }
        #details{
          width: 50%;
        }
    }
    #nav-icon4 span:nth-child(1) {transform-origin: left center;}
    #nav-icon4 span:nth-child(2) {transform-origin: left center;}
    #nav-icon4 span:nth-child(3) {transform-origin: left center;}
    .navigation{
      position: absolute; top: 0%;
    }
    .navList{position: absolute; top: 10%; left: 20px;}
    .navElements{padding: 10px;}
  `],
  animations: [
    trigger('focusSpan1', [
      state('inactive', style({
        top: '0px'
      })),
      state('active', style({
        transform: 'rotate(45deg)',
        top: '-2px',
        left: '4px'
      })),
      transition('inactive => active', animate('250ms ease-in-out')),
      transition('active => inactive', animate('250ms ease-in-out'))
    ]),
    trigger('focusSpan2', [
      state('inactive', style({
        top: '9px'
      })),
      state('active', style({
        width: '0%',
        opacity: '0'
      })),
      transition('inactive => active', animate('250ms ease-in-out')),
      transition('active => inactive', animate('250ms ease-in-out'))
    ]),
    trigger('focusSpan3', [
      state('inactive', style({
        top: '18px'
      })),
      state('active', style({
        transform: 'rotate(-45deg)',
        top: '18px',
        left: '4px'
      })),
      transition('inactive => active', animate('250ms ease-in-out')),
      transition('active => inactive', animate('250ms ease-in-out'))
    ]),
    trigger('navDiv', [
      state('inactive', style({
        right: '-200px'
      })),
      state('active', style({
        right: '200px'
      })),
      transition('inactive => active', animate('250ms ease-in-out')),
      transition('active => inactive', animate('250ms ease-in-out'))
    ])
  ]
})
export class AppComponent {

  state: string = 'inactive';

  toggleNav(){
    this.state = (this.state === 'inactive' ? 'active' : 'inactive');
  }

  clickOff(){
    if(this.state === 'active'){
      this.state = 'inactive';
    }
  }
}


