import { 
  Component,
  trigger,
  state,
  style,
  transition,
  animate,
  keyframes
  } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subscription } from "rxjs/Rx";
import { CoursesService } from './courses.service';
import 'rxjs/add/operator/map';
import { Details } from './details';

@Component({
  selector: 'app-courses',
  templateUrl: './courses.component.html',
  styleUrls: ['./courses.component.scss'],
  animations: [
    trigger('focusCourseBelt', [
      state('inactive', style({
        left: '0%'
      })),
      state('active', style({
        left: '-100%'
      })),
      transition('inactive => active', animate('600ms ease-in-out')),
      transition('active => inactive', animate('600ms ease-in-out'))
    ]),
    trigger('focusCourseContainer', [
      state('inactive', style({
        display: 'none'
      })),
      state('active', style({
        display: 'block'
      })),
      transition('active => inactive', animate('600ms'))
    ])
  ]
})

export class CoursesComponent {
private subscription: Subscription;
courses: any;
results: any;
details: Details;
state: string = 'inactive';

  constructor(private _coursesService: CoursesService) {
    this.courses = _coursesService.getCourses()
    .subscribe((results) => {
      this.results = results;
    });
  }

    toggle(){
      this.state = (this.state === 'inactive' ? 'active' : 'inactive');
    }
    

  getDetails(event){
      if (!this.results){return;}
      let target = event.target || event.srcElement || event.currentTarget;
      let idAttr = target.attributes.id;
      let value = idAttr.nodeValue;
        this._coursesService.getCourseDetails(value)
        .subscribe((details) => {
          this.details = new Details;
          this.details.id = details.id;
          this.details.name = details.name;
          this.details.content = details.content;
          this.details.url = details.url;
      })
      }
}



